.yb2{

  &-result-window{
    background: #000000;
    border: 5px solid #ddd;
    padding: 20px;
    min-height: 300px;
    margin-right: 20px;
    color: #fff;
    font-family: monospace;
    box-sizing: border-box;
    h2{
      margin: 0 0 10px;
      padding-bottom: 10px;
      border-bottom: 1px dashed #fff;
      color: #fff;
    }

    .toggle{
      display: none;
    }

    &.loading{
      position: relative;
      &:before{
        position: absolute;
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 99;
        background: rgba( 255, 255, 255, .4 );
      }
      &:after{
        z-index: 100;
        position: absolute;
        display: block;
        content: "Loading...";
        font-size: 40px;
        top: 50%;
        left: 50%;
        transform: translate( -50%, -50% );
      }
    }
  }

  &-message, &-error{
    font-weight: bold;
    margin-bottom: 10px;
  }

  &-message{
    color: #00a000;
  }

  &-item{
    &-success, &-error{
      list-style: none;
      position: relative;
      padding-left: 40px;
      &:before{
        font-family: "Dashicons";
        display: block;
        width: 40px;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        position: absolute;
      }
    }
    &-success:before{
      content: "\f147";
      color: #00a000;
    }
    &-error:before{
      content: "\f335";
      color: red;
    }
  }

  &-messages{
    margin-left: 0;
    a:link, a:visited{
      font-weight: bold;
      color: inherit;
      text-decoration: underline;
    }
    a:hover, a:active{
      font-weight: bold;
      color: inherit;
      text-decoration: none;
    }
  }

  &-error{
    p{
      color: red;
    }
  }


  &-meta{

    &-col{
      box-sizing: border-box;
      width: 45%;
      margin-right: 5%;
      float: left;
      @media only screen and (max-width: 768px){
        width: 100%;
        float: none;
        margin-right: 0;
      }
      input{
        width: 100%;
        font-weight: normal;
      }
    }

    &-label{
      font-weight: bold;
    }

    &-footer{
      padding: 10px 0 0;
      clear: both;
    }

  }

}